import * as React from "react"
import { NewsItem } from "../data/news"

import "./News.less"


type Props = {
  news: Array<NewsItem>,
  limit: number,
}

const News: React.FC<Props> = ({ news, limit}) => {

  const shows: Array<React.ReactNode> = []
  const hides: Array<React.ReactNode> = []
  news.forEach((v, i) => {
    const element = (
      <div className="news-item" key={'key' + i + '-' + v.year + v.date}>
        <div className="news-year"> {v.year} </div>
        <div className="news-date"> {v.date || ''} </div>
        <div className="news-content">
          {v.content}
        </div>
      </div>
    );
    (i < limit ? shows : hides).push(element)
  })

  return (
    <div className="news">
      {shows}
      <details className="news-root">
        <summary> 显示更多新闻 </summary>
        {hides}
      </details>
    </div>
  )
}

export default News
