import * as React from "react"
import { HeadFC, Link, PageProps } from "gatsby"

import "./index.less"


type Props = {
  children: any,
}

const Layout: React.FC<Props & React.HTMLProps<HTMLDivElement>> = ({ children }) => {
  return (
    <div className="frame">
      <header>
        <div className="header">

          <h1> XDU-ETVP </h1>
          <nav>
            <Link to="/"> 首页 </Link>
            <Link to="/team/"> 团队 </Link>
          </nav>
        </div>
      </header>

      <main>
        {children}
      </main>

      <footer>
        <p> 嵌入式技术与视觉处理研究中心 </p>
        <p> 西安电子科技大学 </p>
        <p className="gray" > 陕西省西安市太白南路2号 </p>
        <p className="gray" > Copyright (C) {new Date().getFullYear()} </p>
      </footer>
    </div>
  )
}

export default Layout