import * as React from "react"
import type { HeadFC, PageProps } from "gatsby"

import "./Box.less"


type Props = {
  title: string,
  children: any,
  className?: string
}

const Box: React.FC<Props> = ({ title, children, className }) => {
  return (
    <div className="box">
      <div className="box-title"> {title} </div>
      <div className="box-contents">
        <div className={className}>
          {children}
        </div>
      </div>
    </div>
  )
}

export default Box