import * as React from "react"
import type { HeadFC, PageProps } from "gatsby"

import Box from "../../components/Box"

import "./Intro.less"


type Props = {
}

const Intro: React.FC<Props> = ({ }) => {


  return (<Box title="简介" className='intro'>
    <p>&nbsp;</p>
    <div className="title">
      <p className="cn">
        <strong>
          西安电子科技大学嵌入式技术与视觉处理研究中心
        </strong>
      </p>
      <p className='en'>
        <strong>
          Embedded Technology and Visual Processing Research Center, Xidian Univ.
        </strong>
      </p>
    </div>
    <div className="content">
      <p>
        西安电子科技大学嵌入式技术与视觉处理研究中心（XDU-ETVP），成立于2015年10月。研究中心依托于原西电软件学院大数据技术和嵌入式系统实验室基础成立，重点关注深度学习技术、人机自然交互技术、机器人复杂场景中环境感知与场景理解、深度网络可解释性等热点研究方向。
        <span className="highlight1"> <strong>
          研究中心目前教授副教授6人、兼职教授5人，具有博士硕士研究生50余人，同国内外知名研究机构帝国理工大学、慕尼黑工业大学、UCLA、清华大学、北京大学、国防科技大学、浙江大学等形成了深入的合作关系。团队已经毕业研究生120余名，主要在各大互联网企业及国家重点研究机构，多名同学赴国内外知名研究机构进行博士深造。
        </strong></span>
      </p>
    </div>
  </Box>)
}

export default Intro